





































import { Component, Vue, Watch } from 'vue-property-decorator';

import InputField from '@/components/InputField.vue';
import { Page, StringKeyValueDict, CreatePageAction, Error } from '@/interfaces';

@Component({
    components: {
        InputField
    }
})
export default class PagesCreate extends Vue {
    public addMode = 'first';
    public pageName = '';
    public pageTitle = '';
    public parentPageId = '';
    public errors: StringKeyValueDict = {};

    public get pages(): Page[] {
        return Object.values(this.$store.state.pages);
    }

    public get hasPages() {
        return this.pages && this.pages.length > 0;
    }

    public get showPageSelect() {
        return this.hasPages && this.addMode === 'after';
    }

    public mounted() {
        if (this.hasPages) {
            this.addMode = 'after';
            this.parentPageId = this.pages[0].id;
            for (let idx = 0; idx < this.pages.length; idx++) {
                if (this.pages[idx].relationships.next.data.length === 0) {
                    this.parentPageId = this.pages[idx].id;
                    break;
                }
            }
        }
    }

    @Watch('pages')
    public updateAddMode() {
        if (this.hasPages) {
            this.addMode = 'after';
            this.parentPageId = this.pages[0].id;
            for (let idx = 0; idx < this.pages.length; idx++) {
                if (this.pages[idx].relationships.next.data.length === 0) {
                    this.parentPageId = this.pages[idx].id;
                    break;
                }
            }
        } else {
            this.addMode = 'first';
        }
    }

    public cancelDialog(allowParent: boolean, ev: Event) {
        const target = ev.target as HTMLElement;
        if (!allowParent && (target.localName === 'a' || target.localName === 'button')) {
            this.$router.push('/pages');
        } else if (allowParent && target.localName === 'div' && target.classList.contains('dialog-overlay')) {
            this.$router.push('/pages');
        }
    }

    public async createPage() {
        try {
            const newPage = {
                mode: this.addMode,
                name: this.pageName,
                title: this.pageTitle,
            } as CreatePageAction;
            if (this.addMode === 'after') {
                newPage.parentPageId = this.parentPageId;
            }
            const page = await this.$store.dispatch('createPage', newPage);
            this.$router.push('/pages/' + page.id);
        } catch(error) {
            this.errors = {};
            error.forEach((entry: Error) => {
                const pointer = entry.source.pointer.split('/');
                this.errors[pointer[pointer.length - 1]] = entry.title;
            });
        }
    }
}
